$mobile-breakpoint: 497px;

.card-grid-options {
  justify-self: end;
  display: flex;
  flex-wrap: wrap;
  color: #ff0000;
  font-size: 1rem;
  justify-content: center;
  margin-bottom: 30px;

  &--loading {
    opacity: 0.7;
  }

  div {
    display: flex;
    align-items: center;
  }

  &__order {
    color: #333;
    img {
      height: 25px;
    }

    margin-bottom: 10px;

    span {
      margin: 0px 15px;
    }

    @media (min-width: $mobile-breakpoint) {
      margin-bottom: unset;
    }

    &--active {
      color: #f00;
    }
  }

  &__favorite {
    justify-content: flex-end;
    img {
      margin-right: 15px;
    }

    @media (min-width: $mobile-breakpoint) {
      margin-left: 30px;
    }
  }

  @media (min-width: $mobile-breakpoint) {
    margin-bottom: unset;
  }
}
